
.hd-footer{ height: 360px; background-color: #fafafc;margin-top: 100px;}
.hd-footer-box{max-width: 1200px; min-width: 1000px; margin:auto;font-size: 12px;}
.hd-footer-after{ height: 140px; border-bottom: 1px solid #ddd; }
.hd-footer-after li{ height: 100%; width: 20%; float: left; text-align: center; color: #6e6e6e; }
.hd-footer-after li i{display: block;padding-top: 20px;font-size: 3em;margin-bottom: 15px;}
.hd-footer-links{height: 180px; border-bottom: 1px solid #ddd;}
.hd-footer-links-left{ width: 80%; float: left; }
.hd-footer-links-phone{ width: 20%; float:right; }
.hd-footer-links-item{float: left;width: 25%;}
.hd-footer-links-item b{display: block;padding: 30px 0 10px 60px;}
.hd-footer-links-sub{padding-left: 60px;}
.hd-footer-links-sub li{line-height: 30px;}
.hd-footer-links-sub li a{ color: #666; }
.hd-footer-links-sub li a:hover{color: #31a2e4;}
.hd-footer-links-phone{ color: #666; }
.hd-footer-links-phone b{display: block;padding: 30px 0 10px 80px; color: #000;}
.hd-footer-links-phone p{padding: 5px 0 10px 80px;}
.hd-footer-links-phone p span{display: block;}
.hd-footer-copyright{line-height: 40px; color: #999;font-size: 12px;}
.hd-footer-copyright a{ margin: 0 10px; color: #666; }
.hd-footer-copyright a:hover{ color: #333; text-decoration: underline; }



