
.hd-header{overflow: hidden;}
.hd-top{height: 35px; width: 100%;background-color: #f1f1f1; color: #666;font-size: 12px;line-height: 35px;}
.hd-top-box{max-width: 1200px;min-width: 1000px;margin: auto;}
.hd-top-link{float: left; position: relative;}
.hd-top-link a{ color: #666;margin-right: 20px; }
.hd-top a:hover{color: #0a72ac; }
.hd-top-app{position:absolute; height: 100px; left: 142px;top: 35px;border:5px solid #eee; }
.hd-top-login{float: right;}
.hd-top-login a{margin-left: 20px; cursor:pointer; color: #666;}
.hd-head{height: 125px;position: relative;max-width: 1200px;min-width: 1000px;margin: auto;border:1px solid #fff;}
.hd-logo{ padding-top:25px ; position: absolute;left: 0;top: 0;}
.hd-search{ width: 500px; margin: 40px auto;}
.hd-search-key{width: 400px;line-height: 35px; height: 35px; padding: 0 5px; border: 2px solid #0a72ac;}
.hd-search-btn{height: 39px; border: none;background-color: #0a72ac; color: #fff; width: 65px; border-top-right-radius: 3px;border-bottom-right-radius: 3px; cursor:pointer;margin-left: -2px;}
.hd-search-btn:hover{background-color: #129be7;}
.hd-search-btn i{font-size: 1.5em;}
.hd-search-label a{font-size: 12px; margin-right: 20px; color: #999;}
.hd-search-label a:hover{color: #129be7;}
.hd-cart{height: 35px;  width: 145px; line-height: 35px;position: absolute;right: 0;top: 42px;border:1px solid #eee; background-color: #f9f9f9; text-align: center; font-size: 12px;}
.hd-cart b{color: #ee5757; margin-left: 5px;}
.hd-cart:hover{background-color: #eee;}
.hd-cart a{ color: #666; }



