
.hd-addtocart{max-width: 1200px; min-width: 1000px; margin:20px auto;font-size: 12px; position: relative; overflow: hidden;}
.hd-addtocart-left{ width:80%; margin-right: 2%;  float: left; }
.hd-addtocart-right{ width: 18%; float: right;}
.hd-addtocart-suc{ height: 100px; line-height: 100px; font-size: 1.5em; color: #7abd54; text-align: center;}
.hd-addtocart-suc-check{ color: #fff;background-color: #e33b3e; display: inline-block;line-height: 20px;font-size: 12px; padding: 05px 20px; margin: 0 20px; border-radius: 5px; }
.hd-addtocart-suc-check:hover{background-color: #cd2528;}
.hd-addtocart-suc-continue{font-size: 12px; color: #666;}
.hd-addtocart-suc-continue a{ color: #129be7; }
.hd-addtocart-suc-continue a:hover{ color: #0a72ac; }
.hd-addtocart-like{ border:1px solid #ddd; overflow: hidden;margin-top: 20px;  }
.hd-addtocart-like-title{ height: 30px;line-height: 30px; font-size: 12px; color: #999; background-color: #f7f7f7; padding-left: 10px; }
.hd-addtocart-like-box{ overflow: hidden;}
.hd-addtocart-like-item{ float: left; width: 30%; margin:15px 1.5%; }
.hd-addtocart-like-item img{ height: 100px; width: 100px; float: left;padding-right: 5px; }
.hd-addtocart-like-item-text{ color: #999;height: 34px; overflow: hidden; }
.hd-addtocart-like-item-price{ margin: 10px 0; color: #0a72ac; font-weight: 600; }
.hd-addtocart-like-item-add{background-color: #f7f7f7; border:1px solid #ddd; display: inline-block;height: 24px;line-height: 24px; width: 100px; text-align: center; }
.hd-addtocart-like-item-add a{color: #666;display: block;  }
.hd-addtocart-like-item-add a:hover{ color: #129be7;background-color: #eee; }
.hd-addtocart-cart{border:1px solid #ddd; border-top: 2px solid #999;}
.hd-addtocart-cart-title{ height: 30px; line-height: 30px; color: #666; background-color: #f7f7f7; padding-left: 10px; }
.hd-addtocart-cart-box{ overflow: hidden; }
.hd-addtocart-cart-item{ border-bottom: 1px dotted #ddd; padding: 10px; overflow: hidden; }
.hd-addtocart-cart-item img{ width: 60px; height: 60px; float: left;padding-right: 5px;  }
.hd-addtocart-cart-item-text{ color: #666;word-break: break-all; }
.hd-addtocart-cart-item-price{ color: #da2525; font-weight: 600; margin-top: 5px; font-weight: 500; }
.hd-addtocart-total{ padding: 10px; background-color: #f7f7f7; color: #666; }
.hd-addtocart-total-price b{ color: #da2525; font-weight: 600;}
.hd-addtocart-total-check{ display: inline-block; height: 35px; line-height: 35px; color: #fff; border-radius: 5px;background-color: #e33b3e; width: 100%; margin:15px auto; text-align: center; }
.hd-addtocart-total-check:hover{background-color: #cd2528;}



