
.hd-nav{ width: 100%;height: 40px; border-bottom: 2px solid #0a72ac; }
.hd-box{max-width: 1200px;min-width: 1000px; margin: auto;position: relative;}
.hd-nav-side{height: 40px; width: 220px;line-height: 40px; text-align: center; position: absolute;left: 0;top: 0; background-color: #0a72ac; color: #fff;}
.hd-nav-side i{ margin-left: 20px; cursor: pointer;}
.hd-nav-main{width: 100%; height: 40px;}
.hd-nav-main ul{padding-left: 220px;}
.hd-nav-main li{width: 16.6%; float: left; text-align: right; height: 40px; line-height: 40px; }
.hd-nav-main li a{ color: #333; }
.hd-nav-main li a:hover{ color: #0a72ac; }
.hd-nav-main li a.active{ color: #0a72ac; }
.hd-dropdown{background-color: #129be7;font-size: 12px; width: 220px; position: absolute; left: 0;top: 42px;min-height: 300px;}
.hd-dropdown li{ height: 40px;line-height: 40px; color: #fff;  position: relative; border-bottom: 1px dotted #87bddb;}
.hd-dropdown li a{ color: #fff; display: block;  padding: 0 10px;}
.hd-dropdown li a:hover{ color: #0a72ac; }
.hd-dropdown li a i{position: absolute; right: 10px;font-size: 0.9em;}



