
.hd-payment-manage-title{height: 30px; line-height: 30px; border-bottom:1px solid #ddd; font-weight: 600; font-size: 1.2em;margin-bottom: 20px; }
.hd-payment-manage-box h3{margin-bottom: 20px;font-size: 1em;}
.hd-payment-manage-box h3 span{ margin-left: 50px; font-weight: 400; color: #666; }
.hd-payment-manage-box h3 span b{ color: #da2525; margin: 0 5px; }
.hd-payment-manage-item{ border:1px solid #ddd; float: left;width: 30%; margin-right: 3%; margin-bottom: 10px; }
.hd-payment-manage-item:hover{background-color: #fafafa;}
.hd-payment-manage-item-info{ padding: 10px; } 
.hd-payment-manage-item-info p{ line-height: 35px; }
.hd-payment-manage-item-info p span{ display: inline-block; width:100px; color: #999;  }
.hd-payment-manage-item-action a{ margin-right: 20px; color: #0b72ac; }
.hd-payment-manage-item-action a:hover{ color: #129be7; text-decoration: underline; }
.hd-payment-manage-add{ text-align: center; border:1px solid #ddd; float: left;width: 30%; margin-right: 3%; height: 160px;}
.hd-payment-manage-add a{ color: #129be7; }
.hd-payment-manage-add a:hover{ color: #0b72ac;}
.hd-payment-manage-add a i{font-size: 3em; display: block; margin-top: 30px;}



