
.hd-address-title{ height: 30px; line-height: 30px; font-weight: 600; font-size: 1.2em;border-bottom: 1px solid #ddd; }
.hd-address-btn{margin: 30px 0; color: #999;}
.hd-address-btn a{ padding: 8px 20px; background-color: #288dcd; color: #fff; margin-right: 20px; border-radius: 4px;}
.hd-address-btn a:hover{background-color: #0b72ac;}
.hd-address-btn b{color: #0b72ac; margin: 0 2px;}
.hd-address-management-item{ border-radius: 4px; border: 4px solid #e6e6e6; padding: 10px;margin-bottom: 20px;}
.hd-address-management-item p{ line-height: 25px; }
.hd-address-management-item p span{display: inline-block; width: 60px; color: #666;}
.hd-address-management-item p a{ color: #0b72ac;margin-left: 20px; }
.hd-address-management-item p a:hover{ color: #129be7; text-decoration: underline; }
.hd-address-management-item-action{text-align: right;}



