/* CSS Reset */
html, body, div, span, object, iframe,h1, h2, h3, h4, h5, h6, p, blockquote, pre,abbr, address, cite, code,del, dfn, em, img, ins, kbd, q, samp,small, strong, sub, sup, var,b, i,dl, dt, dd, ol, ul, li,fieldset, form, label, legend,table, caption, tbody, tfoot, thead, tr, th, td,article, aside, canvas, details, figcaption,figure, footer, header, hgroup, menu, nav, section, summary,time, mark, audio, video {margin:0;padding:0;border:0;outline:0;}
article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary {display:block;}
input, select,button {vertical-align:middle; outline:none;}
table {border-collapse:collapse;border-spacing:0;}
body{font:normal 500 100%/1.45 "Myriad Set Pro","Lucida Grande","Helvetica Neue","Helvetica","Arial","Verdana","sans-serif";position:relative;}
a{ text-decoration:none; outline:none;}
a:visited,a:active{ text-decoration:none;}
i{ margin-right:5px;text-decoration: none;}
.fn-clear:after {visibility:hidden;display:block;font-size:0;content:" ";clear:both;height:0;}
.fn-clear {zoom:1;}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {color: #aaa;}
input:-moz-placeholder, textarea:-moz-placeholder {　color:#aaa;}
input::-moz-placeholder, textarea::-moz-placeholder {　color:#aaa;}　
input:-ms-input-placeholder, textarea:-ms-input-placeholder {color:#aaa;} 
li{ list-style:none;}

/* from validate */
.hd-validate-cnt{margin: 10px 0;}
.hd-validate-error{color:#e50b0b;}
.hd-validate-valid{color:#16a83f;}
.hd-validate-warning{color:#db9703;}
.hd-validate-tips{color:#999;}



/* iconfont */
@font-face{font-family:"iconfont";
src:url('../fonts/iconfont.eot'); /* IE9 */
src:url('../fonts/iconfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
url('../fonts/iconfont.woff') format('woff'), /* chrome、firefox */
url('../fonts/iconfont.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+ */
url('../fonts/iconfont.svg#iconfont') format('svg');} /* iOS 4.1- */

.iconfont{font-family:"iconfont"!important;font-size:16px;font-style:normal;-webkit-font-smoothing:antialiased;-webkit-text-stroke-width:0.2px;-moz-osx-font-smoothing:grayscale;}


/* transition */
.fade-transition {-webkit-transition:opacity .3s ease;transition: opacity .3s ease;}
.fade-enter, .fade-leave {opacity: 0;}

.staggered-transition {-webkit-transition:all .5s ease;transition: all .5s ease; height: auto;}
.staggered-enter, .staggered-leave {opacity: 0;height: 0;}


/* 旋转 */
.iconspin {
    -webkit-animation: iconspin 2s infinite linear;
    animation: iconspin 2s infinite linear
}

@-webkit-keyframes iconspin {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg)
    }
    100% {
        -webkit-transform: rotate(359deg);
        transform: rotate(359deg)
    }
}

@keyframes iconspin {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg)
    }
    100% {
        -webkit-transform: rotate(359deg);
        transform: rotate(359deg)
    }
}


.slideDown-transition {
  -webkit-transition:all .5s ease;
  transition: all .5s ease; height: auto;
}
.slideDown-enter,.slideDown-leave {
  opacity: 0;height: 0;
}







