
.hd-support{max-width: 1200px; min-width: 1000px; margin:20px auto;min-height: 800px;position: relative;font-size: 12px;}
.hd-support-sidebar{ position: absolute; left: 0; top: 0; width: 220px; height: 100%; border-right: 1px solid #eee; }
.hd-support-sidebar h3{ height: 40px; line-height: 40px; text-align: center; color: #fff; font-weight: 400; background-color: #0b72ac; }
.hd-support-sidebar li{line-height: 40px; }
.hd-support-sidebar li .title{display: block; height: 35px; line-height: 35px; position: relative; background-color: #fafafa; padding-left: 35px; color: #555; font-weight: 600; border-bottom: 1px solid #eee;}
.hd-support-sidebar li .title:hover{background-color: #eee;}
.hd-support-sidebar li .title i{ font-size: 0.8em; position: absolute; right: 10px; color: #aaa; }
.hd-support-sidebar li .title.active{background-color: #ddd;}
.hd-support-content{margin-left: 250px;}
.hd-support-sidebar li li{border-bottom: 1px dotted #eee;}
.hd-support-sidebar li li a{ display: block; padding-left: 35px; color: #666; }
.hd-support-sidebar li li a:hover{ color: #000;}
.hd-support-sidebar li li a.active{ color: #129be7;}



