
.hd-about-info{ padding-bottom: 20px;}
.hd-about-info-item{display: none;}
.hd-about-info-info{border-left: 1px solid #eee;min-height: 600px; margin-left: 240px;padding-left: 30px;}
.hd-about-info-img{overflow: hidden; float: left;width: 60%;height:auto;margin:0 20px 0 0; }
.hd-about-info-text-item{margin-bottom: 20px;}
.hd-about-info-text-item h3{font-size: 1.5em;}
.hd-about-info-text-item p{margin: 10px 0;line-height: 20px; color: #333;}
.hd-about-info-text-item .certificate{text-align: center;padding-bottom: 20px;}
.hd-about-info-text-item .certificate img{ width:20%;min-width: 250px;border: 1px solid #eee;margin:0 20px;}




