
.hd-info-warpper{max-width: 1200px; min-width: 1000px;margin:20px auto;font-size: 12px; }
.hd-info-product{overflow: hidden;}
.hd-info-pic{ float: left;}
.hd-info-pic-max{width: 500px; height: 500px; border:1px solid #eee; }
.hd-info-pic-min li{ float: left;margin-right: 5px; }
.hd-info-pic-min img{ width: 50px; height: 50px; border:1px solid #eee; }
.hd-info-pic-min img.active{border-color: #129be7;}
.hd-info-text{ margin-left: 550px; }
.hd-info-text-data{ border-bottom: 1px solid #ddd; padding-bottom: 10px; margin-bottom: 20px; }
.hd-info-text-title{ font-size: 2em;}
.hd-info-text-label{ color: #666;margin: 15px 0; }
.hd-info-text-price{font-size: 1.5em;color: #da2525; }
.hd-info-text-attribute{font-size: 14px;}
.hd-info-text-item{margin-bottom: 20px; overflow: hidden;}
.hd-info-text-item dd{ display: inline-block; height: 30px; line-height: 30px; text-align: center; border:1px solid #ddd; margin:5px 10px 0 0; padding: 0 10px; cursor: pointer; }
.hd-info-text-item dd.active{border-color: #129be7;}
.hd-info-text-total{font-size: 14px;}
.hd-info-total-detail{padding: 5px 0; color: #666; text-decoration: underline;}
.hd-info-total-price{margin: 10px 0;}
.hd-info-total-price b{font-size: 2em;color: #da2525; font-weight: normal;}
.hd-info-buy{ color: #999;margin-top: 30px; }
.hd-info-buy-btn{ height: 40px; padding: 0 40px; background-color: #e2393c; color: #fff; cursor: pointer; border:none; border-radius: 5px;margin-right: 20px; }
.hd-info-buy-btn:hover{ background-color: #cd2528; }
.hd-info-tab{margin-top: 30px;}
.hd-info-tab-link{font-size: 14px; border-bottom: 1px solid #ddd; height: 40px;line-height: 40px;}
.hd-info-tab-link a{display: inline-block; width: 150px; text-align: center; color: #000;}
.hd-info-tab-link a.active{ color: #129be7; }



