
.hd-personal-title{height: 30px; line-height: 30px; border-bottom: 1px solid #ddd; font-size: 1.2em; font-weight: 600;}
.hd-personal-info-box{margin: 20px 0;}
.hd-personal-sidebar{ height: 30px; line-height: 30px;margin-bottom: 20px; }
.hd-personal-sidebar a{ color: #666;margin-right: 30px; padding: 8px 20px;background-color: #f9f9f9; }
.hd-personal-sidebar a.active{ color: #129be7; background-color: #eee; }
.hd-personal-basic,.hd-personal-password{border-top: 1px solid #eee; padding: 20px 0;}
.hd-personal-basic-item {margin-bottom: 20px;}
.hd-personal-basic-item .title{ display:inline-block; width: 150px; text-align: right;padding-right: 20px;  }
.hd-personal-basic-item .title em{color: #e30c0c;}
.hd-personal-basic-item  input[type='text'],.hd-personal-basic-item  input[type='passord']{ padding: 5px;border: none; width: 300px; border-bottom: 1px solid #ddd; }
.hd-personal-btn{ background-color: #0b72ac; color: #fff; padding: 8px 40px; border-radius: 4px; border: none; cursor: pointer;margin-top: 20px; }
.hd-personal-btn:hover{background-color: #129be7;}
.hd-personal-password-tips{ line-height: 25px; color: #999;margin-bottom: 10px; }



