
.hd-error{background-color: #f6f6f6;margin-bottom: -100px;height: 700px;} 
.hd-error-cnt{max-width: 1200px;min-width: 1000px; height: 100%; margin: auto; background: url('../../assets/images/404.jpg') 100px 200px no-repeat;}
.hd-error-cnt-info{ padding: 240px 0 0 440px; }
.hd-error-cnt-info h1{font-size: 3em;font-weight: 400;}
.hd-error-cnt-info  .tips{ margin-bottom: 30px; color: #666; }
.hd-error-cnt-info a{ padding: 10px 30px; background-color: #0a72ac; color: #fff; border-radius: 4px;font-size: 12px; }
.hd-error-cnt-info a:hover{background-color: #129be7;}



