
.hd-account-info{ height: 135px;; background-color: #eee; overflow: hidden;}
.hd-account-info-left{ float: left; padding: 10px; width: 330px; }
.hd-account-info-right{ float: right; padding: 16px; }
.hd-account-info-pic{ float: left; padding-top: 15px;}
.hd-account-info-pic img{ width: 80px; height: 80px; border-radius: 50%;}
.hd-account-info-account{ float: left; margin-left: 30px; padding-top: 30px; }
.hd-account-info-account .name { font-weight: 600; font-size: 1.5em; }
.hd-account-info-account .email{ line-height: 25px; }
.hd-account-info-order{ background-color: #fafafa; width: 480px; height: 80px; padding: 10px; }
.hd-account-info-order .title{ line-height: 25px; color: #aaa; }
.hd-account-info-order span{ display: block; width: 50%; float: left; line-height: 25px; }
.hd-account-info-order span b{ color: #da2525; margin-left: 5px;}
.hd-account-order{ margin: 30px 0; }
.hd-account-order h3{line-height: 30px; border-bottom: 1px solid #ddd;}
.hd-account-order-item{ background-color: #edfafe; border-bottom: 1px solid #eee; height: 140px; margin-bottom: 20px; color: #666; }
.hd-account-order-item div{ float: left; }
.hd-account-order-item-pic,.hd-account-order-item-price,.hd-account-order-item-num,.hd-account-order-item-time,.hd-account-order-item-action{ width: 14%; }
.hd-account-order-item-price,.hd-account-order-item-num,.hd-account-order-item-time,.hd-account-order-item-action{margin-top: 60px;}
.hd-account-order-item-text{ width: 30%; margin-top: 40px; }
.hd-account-order-item-text p{ padding: 0 5px; }
.hd-account-order-item-pic img{ width: 90px; height: 90px; border: 1px solid #eee; margin: 25px 10px; }
.hd-account-order-item-price b{color: #da2525; font-weight: 500;}
.hd-account-order-item-action{ text-align: center; }
.hd-account-order-item-action a{ color: #129be7; }
.hd-account-order-item-action a:hover{ color: #0b72ac; text-decoration: underline; }
.hd-account-like-title{font-weight: 600; font-size: 14px; line-height: 40px; border-bottom: 1px solid #ddd;margin-bottom: 20px;}
.hd-account-like-box{overflow: hidden;}
.hd-account-like-item{ float: left; width: 25%; border-bottom: 1px solid #eee; text-align: center; }
.hd-account-like-item img{ width: 150px; height: 150px; }
.hd-product-info-text{max-height: 51px; overflow: hidden;}
.hd-account-like-item a:hover p{ color: #129be7; }



