
.hd-product-banner{ height: 350px; width: 100%; overflow: hidden; }
.hd-product-box{max-width: 1200px; min-width: 1000px; margin: auto;position: relative;}
.hd-banner-pic{width: 100%; height: 100%; overflow: hidden;}
.hd-product-sidebar{height: 350px; width: 220px;background-color: #129be7;position: absolute;left: 0; top: 0;}
.hd-product-sidebar-item{ height: 40px; line-height: 40px;border-bottom: 1px dotted #87bddb;}
.hd-product-sidebar-item a{display: block; padding: 0 10px;color: #fff; position: relative;font-size: 13px; }
.hd-product-sidebar-item a i{position: absolute; right: 10px; font-size: .9em;}
.hd-product-sidebar-item a:hover{ color: #9ed9f9; }
.hd-product-content{ overflow: hidden; }
.hd-product-item{margin: 30px 0;font-size: 12px;}
.hd-product-item-title{ height: 35px; line-height: 35px;background-color: #eee; color: #000; position: relative;padding: 0 5px; }
.hd-product-item-title a{position: absolute;right: 10px;color: #129be7;}
.hd-product-item-title a:hover{ color: #0a72ac; text-decoration: underline; }
.hd-product-item-box{overflow: hidden;}
.hd-product-item-info{ float: left;width: 20%; height: 250px; padding: 10px 0; border-bottom: 1px solid #eee; text-align: center; }
.hd-product-item-info a:hover p{ color: #31a2e4;}
.hd-product-item-info img{ height: 160px;}
.hd-product-info-text{height: 51px; padding:0 10px 10px 10px; overflow: hidden; word-break: break-all; color: #999;}
.hd-product-info-price b{ color: #da2525; font-size: 14px;}



