
.hd-about-warpper{max-width: 1200px; min-width: 1000px;margin:20px auto;position: relative;font-size: 12px;min-height: 800px; position: relative; }
.hd-about-sidebar{ height: 100%; width: 220px; border-right: 1px solid #eee; position: absolute;left: 0;top: 0; }
.hd-about-content{ margin-left: 250px}
.hd-about-sidebar li{ height: 40px; line-height: 40px; border-bottom: 1px dotted #eee; }
.hd-about-sidebar li a{ color: #333; display: block; padding-left: 10px; }
.hd-about-sidebar li a:hover{ color:#129be7;  }
.hd-about-sidebar li a.active{ background-color: #fafafa; color: #0b72ac; }



