
.hd-account-top{height: 35px; width: 100%;background-color: #f1f1f1; color: #666;font-size: 12px;line-height: 35px; }
.hd-top-content{max-width: 1200px;min-width: 1000px;margin: auto; position: relative;}
.hd-top-link{float: left;height: 35px;}
.hd-top-link a{ color: #666;margin-right: 20px; }
.hd-top a:hover{color: #0a72ac; }
.hd-top-app{position:absolute; height: 100px; width: 100px; left: 142px;top: 35px;border:5px solid #eee;}
.hd-top-login{float: right;height: 35px;}
.hd-top-login a{margin-left: 20px; cursor:pointer; color: #666;}
.hd-account-menu{background-color: #0b72ac; height: 110px;font-size: 12px;}
.hd-account-box{max-width: 1200px;min-width: 1000px;margin: auto; position: relative;}
.hd-account-logo{ height: 75px; padding-top: 15px; float: left; position: relative;  }
.hd-account-logo img{vertical-align: middle;  }
.hd-account-rihgt{ float: right;padding-top: 40px; }
.hd-account-logo-back{ color: #fff;margin-left: 60px;position: absolute; top: 50px; left: 200px; width: 100px;}
.hd-account-logo-back:hover{ text-decoration: underline; }
.hd-account-search,.hd-account-cart{ float: left; }
.hd-account-search{ width: 225px; height: 30px; background-color: #fff; position: relative; margin-right: 30px; }
.hd-account-search input{ padding: 0 35px 0 5px; width: 185px; height: 100%; border: none; }
.hd-account-search a{position: absolute; top:0; right: 5px; color:#0b72ac;}
.hd-account-search a i{ font-size: 2em;margin-right: 0; }
.hd-account-search a:hover{ color: #129be7; }
.hd-account-cart{background-color: #fff; padding: 0 20px; height: 30px; line-height: 30px; color: #666;}
.hd-account-cart:hover{background-color: #eee;}
.hd-account-cart b{ color: #0b72ac; }
.hd-account-cart a{color: #666;}



