
.hd-address-form-box{ margin: 30px 0; }
.hd-address-form-inline{margin-bottom: 15px;}
.hd-address-form-inline label{ display: block; font-weight: 600; color: #333;font-size: 13px; }
.hd-address-form-inline input[type='text'],.hd-address-form-inline select{ width: 400px; border-radius: 4px; border: 1px solid #ccc; padding:6px 10px; margin-top: 2px;  }
.hd-address-from-tips{ margin-bottom: 10px; }
.hd-address-from-tips h3{font-size: 1.5em; line-height: 30px;}
.hd-address-form-btn{background-color: #0b72ac; padding: 10px 30px; color: #fff; border: none; cursor: pointer; border-radius: 4px;margin-top: 20px;}
.hd-address-form-btn:hover{background-color: #129be7;}



