
.hd-index-content{ overflow: hidden;}
.hd-banner{ height: 450px;max-width: 1200px; min-width: 1000px; margin: auto; position: relative;font-size: 12px; overflow: hidden; }
.hd-banner-sidebar{ position: absolute;left: 0;top: 0; height: 100%; width: 220px;background-color: #129be7; }
.hd-sidebar-item-title{ height: 30px; line-height: 30px; padding-left: 10px;color: #86c3e5; border-bottom: 1px solid #66b8e6;}
.hd-sidebar-item-link{padding:5px 10px;height: 50px; overflow: hidden;}
.hd-sidebar-item-link a{ display: inline-block;margin-right: 10px; color: #fff; line-height: 20px; overflow: hidden; }
.hd-sidebar-item-link a:hover{ text-decoration: underline; }
.hd-banner-box{margin-left:230px;margin-top: 10px; height: 440px; min-width: 770px; max-width: 970px; overflow: hidden; position: relative; }
.hd-banner-list a{position: absolute;left: 0;top: 0;}
.hd-banner-btn{ position: absolute;right: 10px;bottom: 10px; }
.hd-banner-btn li{ float:left; width: 10px;height: 10px;outline: none;border-radius: 50%;background-color: #999;margin: 0 5px;cursor: pointer;display: inline-block; }
.hd-banner-btn li:hover{background-color: #666;}
.hd-banner-btn li.active{background-color: #14a5f5;}
.hd-popular{ height: 200px;max-width: 1200px; min-width: 1000px; background-color: #f5f5f5; margin:10px auto;position: relative; overflow: hidden; }
.hd-popular-title{ position: absolute; left: 0;top: 0; width: 220px; height: 100%; background-color: #c4dbeb; text-align: center; color: #0a72ac;padding-top: 10px; }
.hd-popular-title i{font-size: 5em; display: block;}
.hd-popular-box{ margin-left: 230px; height: 100%;overflow: hidden; font-size: 12px;}
.hd-popular-box li{ float: left; width: 33%; padding-right: .3%;position: relative; }
.hd-popular-box li a{ color: #333; display: block; }
.hd-popular-box li a:hover p{-webkit-transition: all 0.2s ease;transition: all 0.2s ease;color: #31a2e4;border-color: #31a2e4;}
.hd-popular-box li a:hover b{color: #da2525;}
.hd-popular-box li img{width:160px; height: 160px; position: absolute;left: 0;top: 20px;}
.hd-popular-item  p{margin-left: 160px;}
.hd-popular-item-title{ padding-top: 40px; font-size: 1.5em; border-bottom: 1px solid #555; margin-bottom: 10px; color: #333; display: inline-block;}
.hd-popular-item-text{font-size: 12px; color: #666;}
.hd-popular-item b{ padding-top: 10px; color: #f22b2b; font-size: 14px; display: block; }
.hd-newProducts{max-width: 1200px; min-width: 1000px; margin: 20px auto; font-size: 12px;}
.hd-index-title{height: 40px; border-bottom: 1px solid #ddd;margin-bottom: 5px;}
.hd-index-title span{ display: inline-block; width: 30px; height: 20px;line-height: 20px; text-align: center; margin-top: 10px; color: #fff; background-color: #0a72ac; margin-right: 20px; }
.hd-newProducts-box{ height: 280px; border-bottom: 1px solid #ddd; }
.hd-newProducts-item{ width: 20%; height: 100%; overflow: hidden; float: left; text-align: center; }
.hd-newProducts-item img{ height: 180px; margin-bottom: 5px; }
.hd-newProducts-item-title{ color: #000;margin-bottom: 5px; overflow: hidden; }
.hd-newProducts-item-text{ color: #666; overflow: hidden; }
.hd-newProducts-item-price{ color: #da2525; font-size: 14px;margin-top: 10px;}
.hd-newProducts-item a:hover p{ color: #31a2e4; }
.hd-newProducts-item a:hover b{color: #da2525;}
.hd-adv{max-width: 1200px; min-width: 1000px; overflow: hidden; height: 130px; margin: auto; }
.hd-featured{max-width: 1200px; min-width: 1000px;margin:20px auto;font-size: 12px; }
.hd-featured-box{margin-top: -5px;overflow: hidden;} 
.hd-featured-box li{ float: left; height: 400px; width: 20%; text-align: center; }
.hd-featured-box li img{height: 200px; width: auto; padding-top: 30px;padding-bottom: 30px; }
.hd-featured-box li a:hover p{color: #31a2e4; }
.hd-featured-item1{ background-color: #fbfbfb; }
.hd-featured-item2{ background-color: #fdeefb; }
.hd-featured-item3{ background-color: #efe6dd; }
.hd-featured-item4{ background-color: #d6eef6; }
.hd-featured-item5{ background-color: #fce6e8; }
.hd-featured-item-title{ font-size: 14px; color: #000;}
.hd-featured-item-text{ padding-top: 10px; color: #666;}
.hd-featured-item-price{margin-top: 10px;}
.hd-featured-item-price b{color: #da2525;font-size: 14px;}
.hd-brand{max-width: 1200px; min-width: 1000px; margin: auto; font-size: 12px;}
.hd-brand-box{ padding: 5px; background-color: #f5f5f5;margin-top: -5px; overflow: hidden; }
.hd-brand-box a{ display: block;float: left; width: 20%; line-height: 25px; color: #333; }
.hd-brand-box a:hover{ color: #31a2e4; }
.hd-brand-box a span{color: #f22b2b;margin-right: 10px; font-weight: bold;}
.hd-selling{max-width: 1200px;min-width: 1000px; margin:20px auto; font-size: 12px; overflow: hidden;}
.hd-selling-box{margin-top: -5px;height: 300px; border-bottom: 1px solid #ddd;overflow: hidden;position: relative; text-align: center; }
.hd-selling a:hover p{color: #31a2e4;}
.hd-selling-first{width: 234px; height: 300px; position: absolute;left: 0;top: 0; border-right: 1px solid #ddd;}
.hd-selling-first img{ width: 200px; height: 200px;padding-top: 10px; }
.hd-selling-first-text{ color: #666; margin: 10px 0; height: 34px; overflow: hidden; }
.hd-selling-first-price b,.hd-selling-others-price b{color: #da2525;font-size: 14px;}
.hd-selling-others{margin-left: 240px; overflow: hidden;}
.hd-selling-others li{ float: left; height: 150px; width: 25%; text-align: left; }
.hd-selling-others li img{ width: 100px; height: 100px; float: left;padding: 10px 5px 0 0; }
.hd-selling-others-text{ color: #666; padding-top:30px; height: 51px; overflow: hidden;}



