
.hd-agent-title{ height: 30px; font-weight: 600; font-size: 1.2em; border-bottom: 1px solid #ddd; }
.hd-agent-tips{margin: 20px 0; color: #999;}
.hd-agent-support{ background-color: #f7fdff; border: 1px solid #bde3f1; padding: 20px;margin-bottom: 20px; }
.hd-agent-support li{ color: #666; list-style: inside circle; line-height: 30px;}
.hd-agent-form-box{ background-color: #fafafa; border: 1px solid #eee;padding:15px}
.hd-agent-form-tips{ margin-bottom: 10px; color: #666; }
.hd-agent-form-tips b{ color: #ff8080;margin: 0 5px; }
.hd-agent-form-item{margin: 30px 0;}
.hd-agent-form-item label{ display: inline-block; width: 80px; text-align: right; margin-right: 20px; vertical-align: top; }
.hd-agent-form-item label em{ color: #e80f0f; display: inline-block;width: 5px; }
.hd-agent-form-item input[type='text']{ width: 300px; border: none;border-bottom: 1px solid #ddd; padding: 5px 10px;background-color: #fafafa; color: #129be7; }
.hd-agent-form-item textarea{ vertical-align: top; width: 500px; height: 200px; border: 1px solid #ddd; padding: 5px;color: #129be7;  }
.hd-agent-form-item-btn{ padding: 8px 40px; color: #fff; background-color: #129be7; border-radius: 4px;border: none; cursor: pointer; }
.hd-agent-form-item-btn:hover{background-color: #0b72ac;}
.hd-agent-form-submit-tips{padding-left: 100px;margin-bottom: 20px; color: #999;}



