
.hd-support-bread{ height: 30px; line-height: 30px; background-color: #f5f5f5; color: #aaa; }
.hd-support-bread span{ color: #999;margin: 0 10px; }
.hd-support-content-topmenu{margin-bottom: 10px;}
.hd-support-content-topmenu a{display: inline-block; line-height: 15px; padding:  0 20px;border-right: 1px solid #ddd; color: #666; margin:15px 0;}
.hd-support-content-topmenu a.active{color: #0b72ac;text-decoration: underline;}
.hd-support-content-box{border: 1px solid #eee; padding: 15px 20px 30px 20px;margin: 20px 0;}
.hd-support-content-box li{list-style: inside circle;height: 35px; line-height: 35px; color: #999; border-bottom: 1px dotted #ddd;}
.hd-support-content-box li a{ color: #333; display: block; }
.hd-support-content-box li a:hover{ color: #0b72ac; text-decoration: underline; background-color: #fafafa;}



