
.hd-address{max-width: 1200px; min-width: 1000px; margin: auto;font-size: 12px;}
.hd-address-select{ overflow: hidden; }
.hd-address-select-title{ border-bottom: 1px solid #ddd;}
.hd-address-select-title h3{font-size: 1.5em;font-weight: 500;}
.hd-address-select-title p{ color: #999;line-height: 30px; }
.hd-address-select-title p a{ color: #129be7; margin-left: 5px; }
.hd-address-select-title p a:hover{text-decoration: underline; color: #0a72ac;}
.hd-address-box{ overflow: hidden; margin-top: 40px; }
.hd-address-item{ border:1px solid #ddd; float: left;width: 25%; padding: 10px 0; margin-right: 5%; cursor: pointer; margin-bottom: 10px; }
.hd-address-item.active{ border-color: #ed0808; background: url('../../assets/images/select.png') right bottom no-repeat; }
.hd-address-item p{ padding:2px 10px;}
.hd-address-item-name{font-size: 14px; font-weight: 600; }
.hd-address-item-ac{margin: 5px 0}
.hd-address-item-ac a{ color: #129be7; margin-right: 20px; }
.hd-address-item-ac a:hover{ text-decoration: underline;color: #0a72ac;}
.hd-shoppingList{margin-top: 40px;}
.hd-shoppingList-title{ position: relative;border-bottom: 1px solid #ddd;padding-bottom: 10px; }
.hd-shoppingList-title h3{font-size: 1.5em;font-weight: 500;}
.hd-shoppingList-title a{ position: absolute; right: 0; bottom: 5px; color: #129be7;}
.hd-shoppingList-title a:hover{ text-decoration: underline;color: #0a72ac;}
.hd-shoppingList-box{ margin: 20px 0; }
.hd-shoppingList-item{ background-color: #edfafe; padding: 20px 0; overflow: hidden;margin-bottom: 20px;}
.hd-shoppingList-item-pic{float: left; width: 15%; }
.hd-shoppingList-item-pic img{ width: 90px; height: 90px; margin-left: 20px; }
.hd-shoppingList-item-info{ float: left;width: 40%; padding-top: 30px; }
.hd-shoppingList-item-title{ max-height: 34px; overflow: hidden; }
.hd-shoppingList-item-model{ color: #999; }
.hd-shoppingList-item-price,.hd-shoppingList-item-num,.hd-shoppingList-item-fatory{ float: left; width: 15%;padding-top: 40px; }
.hd-shoppingList-item-price b{font-weight: 400; color: #da2525;}
.hd-shoppingList-money{border-top: 1px solid #ddd; text-align: right; padding-top: 15px; overflow: hidden;}
.hd-shoppingList-money p{ line-height: 30px;font-size: 14px; }
.hd-shoppingList-money span{display: inline-block;}
.hd-shoppingList-money span b{ color: #e2383b; }
.hd-shoppingList-money .title{ width: 150px; text-align: right;}
.hd-shoppingList-money .value{min-width: 60px; padding-left: 20px;}
.hd-shoppingList-money-pay{display: block; width: 180px; height: 40px; line-height: 40px; text-align: center; color: #fff; border-radius: 5px; background-color: #e2383b; float: right; margin-top: 10px;}
.hd-shoppingList-money-pay:hover{background-color: #c33033;}
.hd-address-empty{ height: 60px; line-height: 60px; color: #333; text-align: center; border:1px solid #ddd; margin: 20px 0; background-color: #f9f9f9; }
.hd-address-empty a{display: inline-block; height: 25px;line-height: 25px; margin-left: 10px; padding: 0 20px; color: #fff; background-color: #e33b3e; border-radius: 5px;}
.hd-address-empty a:hover{background-color: #cd2528;}



